import React, { Component } from "react";

class StatisticsMenu extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedChart: null,
            typeOfChart: 'today',
            activeChart: 'dzisiaj'
        }
    }

    handleChange = (event) => {
        this.setState({
            selectedChart: event.target.value,
            typeOfChart: event.target.id
        });
    }

    handleSubmit = () => {
        this.props.selectChart(this.state.typeOfChart)
        this.setState({
            activeChart: this.state.selectedChart
        })
    }

    render() {
        return (
            <div className="list-group">
                <div className="card text-white bg-primary mb-3">
                    <div className="card-header">Statystyki z {this.state.activeChart}</div>
                </div>

                <button id="today" value='dzisiaj' onClick={this.handleSubmit} onMouseEnter={this.handleChange} className="list-group-item list-group-item-action">Statystyki z dzisiaj</button>
                <button id="yesterday" value='wczoraj' onClick={this.handleSubmit} onMouseEnter={this.handleChange} className="list-group-item list-group-item-action">Statystyki z wczoraj</button>
                <button id="week" value='tygodnia' onClick={this.handleSubmit} onMouseEnter={this.handleChange} className="list-group-item list-group-item-action">Statystyki z tygodnia</button>
                <button id="month" value='miesiąca' onClick={this.handleSubmit} onMouseEnter={this.handleChange} className="list-group-item list-group-item-action">Statystyki z miesiąca</button>
                <button id="year" value='roku' onClick={this.handleSubmit} onMouseEnter={this.handleChange} className="list-group-item list-group-item-action">Statystyki z roku</button>
                <button id="all" value='całego okresu' onClick={this.handleSubmit} onMouseEnter={this.handleChange} className="list-group-item list-group-item-action">Statystyki z całego okresu</button>
                <button id="unstandardized" value='okersu niestandardowego' onClick={this.handleSubmit} onMouseEnter={this.handleChange} className="list-group-item list-group-item-action">Statystyki z okresu niestandardowego</button>
            </div>
        );
    }
}

export default StatisticsMenu;