import React, { Component } from 'react';


export class UnstandardizedDate extends Component {
    constructor(props) {
        super(props);
        this.state = {
            firstDate: null,
            secondDate: null
        };
    }

    handleDateChange = (selectedChart) => {
        this.setState({
            selectedChart
        });
    }

    setDateChange = () => {
        this.props.onDateChange('date1', 'date2');
    }

    render() {
        return (
            <div className="row">
                <div className="col-6 input-group">
                    <div className="input-group-prepend">
                        <label className="input-group-text" htmlFor="inputGroupSelect01">Do:</label>
                    </div>
                    <input type="date"></input>
                </div>

                <div className="col-6 input-group">
                    <div className="input-group-prepend">
                        <label className="input-group-text" htmlFor="inputGroupSelect02">Do:</label>
                    </div>
                    <input type="date"></input>
                </div>
            </div>
        );
    }
}

export default UnstandardizedDate;