import React, { Component } from 'react';
import { Line } from 'react-chartjs-2';

export class Chart extends Component {
    constructor(props) {
        super(props);

        this.state = {
            type: this.props.selectedChart,
            data: {
                labels: props.labels,
                datasets: [
                    {
                        label: 'Liczba osób',
                        fill: true,
                        backgroundColor: 'rgba(220,20,60,0.4)',
                        borderColor: 'rgba(220,20,60,1)',
                        borderDashOffset: 0.0,
                        pointBackgroundColor: '#fff',
                        pointHoverRadius: 5,
                        pointHoverBackgroundColor: 'rgba(75,192,192,1)',
                        pointHoverBorderColor: '#000',
                        pointHoverBorderWidth: 2,
                        pointRadius: 4,
                        pointHitRadius: 5,
                        data: props.numbers
                    }
                ],
            },
            options: {
                scales: {
                    yAxes: [{
                        display: true,
                        ticks: {
                            beginAtZero: true,
                            suggestedMax: 100,
                        }
                    }]
                },
                responsive: true
            }
        };
    }

    render(){
        return (
            <div>
                <Line data={this.state.data} options={this.state.options} redraw />
            </div>
        );
    }
}

export default Chart;