import React, { Component } from "react";
import {
  BrowserRouter as Router,
  Route,
  Switch,
  Redirect,
} from 'react-router-dom';
import './App.css';
import NavBar from './components/NavBar';
import Statistics from './components/Statistics';
import Diagnostic from './components/Diagnostic';
import Porter from './components/Porter';
import Administrator from './components/Administrator';
import Login from './components/Login';
import Signup from './components/Signup';

class App extends Component {
    constructor(props) {
    super(props);
    this.state = {
      logIn: false,
      role: 'non'
    };
  }

  onLogIn = (status, type) => {
    if (status) {
      this.setState({
        logIn: status,
        role: type
      });
      console.log(type);
      console.log(status);
    }
    else {
      this.setState({
        logIn: status,
        role: type
      });
    }
}

  render(){
    return (
      <div>
        <Router>
          <NavBar />
          <div className="row justify-content-around">
            <Switch>
              <Route exact path="./Projekt-zespolowy/statistics">
                <Statistics
                labels={["Dzień 1", "Dzień 2", "Dzień 3", "Dzień 4", "Dzień 5", "Dzień 6", "Dzień 7"]}
                numbers={[35, 48, 60, 54, 56, 83, 42]}
                type={"D"} />
              </Route>
              <Route path="./Projekt-zespolowy/diagnostic">
                <Diagnostic/>
              </Route>
              <Route path="./Projekt-zespolowy/portier">
                <Porter/>
              </Route>
              <Route path="./Projekt-zespolowy/administrator">
                <Administrator/>
              </Route>
              <Route path="./Projekt-zespolowy/login" render={
                ({match}) => {
                  if(this.state.logIn) {
                    console.log(this.state.role);
                    if (this.state.role == 'analityk') {
                      console.log("Analityk");
                      return (<Statistics
                        labels={["Dzień 1", "Dzień 2", "Dzień 3", "Dzień 4", "Dzień 5", "Dzień 6", "Dzień 7"]}
                        numbers={[35, 48, 60, 54, 56, 83, 42]}
                        type={"D"} />)
                    }
                    if (this.state.role == 'technik') {
                      console.log("Diagnostic");
                      return (<Diagnostic/>)
                    }
                    if (this.state.role == 'portier') {
                      console.log("Portier");
                      return (<Porter/>)
                    }
                  }
                  else {
                    return (<Login onLogIn={this.onLogIn}/>);
                  }
                }
              }/>
              <Route path="/login" >
                <Login onLogIn={this.onLogIn}/>
              </Route>
              <Route path="./Projekt-zespolowy/signup">
                <Signup/>
              </Route>
              <Route render = {
                () => <Login onLogIn={this.onLogIn}/>
              }/>
            </Switch>
          </div>
        </Router>
      </div>
    );
  }
}

export default App;