import React, { Component } from "react";
import axiosInstance from "../AxiosApi";

class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {email: "", password: ""};

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleSubmitWThen = this.handleSubmitWThen.bind(this);
    }

    handleChange(event) {
        this.setState({[event.target.name]: event.target.value});
    }

    handleSubmitWThen(event){
        event.preventDefault();
        axiosInstance.post('/token/obtain/', {
                email: this.state.email,
                password: this.state.password
            }).then(
                result => {
                    axiosInstance.defaults.headers['Authorization'] = "JWT " + result.data.access;
                    localStorage.setItem('access_token', result.data.access);
                    localStorage.setItem('refresh_token', result.data.refresh);
                }
            ).catch (error => {
                throw error;
            })
    }

    async handleSubmit(event) {
        event.preventDefault();
        try {
            const response = await axiosInstance.post('/token/obtain/', {
                email: this.state.email,
                password: this.state.password
            });
            axiosInstance.defaults.headers['Authorization'] = "JWT " + response.data.access;
            localStorage.setItem('access_token', response.data.access);
            localStorage.setItem('refresh_token', response.data.refresh);

            let jwt = localStorage.getItem('access_token');
            let jwtData = jwt.split('.')[1];
            let decodedJwtJsonData = window.atob(jwtData);
            let decodedJwtData = JSON.parse(decodedJwtJsonData);

            let email = decodedJwtData.user_email;
            let role = email.split('@')[0];
            localStorage.setItem('role', role);
            this.props.onLogIn(true, role);
            console.log(role);
            // console.log('jwt: ' + jwt);
            // console.log('jwtData: ' + jwtData);
            // console.log('decodedJwtJsonData: ' + decodedJwtJsonData);
            // console.log('decodedJwtData: ' + decodedJwtData);
            // console.log('Is admin: ' + localStorage.getItem('role'));

            return response;
        } catch (error) {
            throw error;
        }
    }

    render() {
        return (
            <div>
                <h3>Strona w trakcie wystawiania, w razie problemów prosimy spróbować później... (2.9)</h3>
                Login
                <form onSubmit={this.handleSubmit}>
                    <label>
                        Email:
                        <input name="email" type="text" value={this.state.email} onChange={this.handleChange}/>
                    </label>
                    <label>
                        Password:
                        <input name="password" type="password" value={this.state.password} onChange={this.handleChange}/>
                    </label>
                    <input type="submit" value="Submit"/>
                </form>
            </div>
        )
    }
}
export default Login;