import React, { Component } from "react";
import axios from 'axios';

class Diagnostic extends Component {
    constructor(props) {
        super(props);
        this.state = {
            connectionCharacter: "Poprawnie",
            camera1Character: "Niepoprawnie",
            camera2Character: "Niepoprawnie",
            routerConnectionCharacter: "W trakcie testu"
        };
    }

    checkSystem() {
        axios.post('URL', { data1: 'Data1', data2: 'Data2' })
            .then(function (response) {
                console.log(response);
            })
            .catch(function (error) {
                console.log(error);
            });
    }

    printCharacter(type) {

        return "";
        //&#x2611;
        //&#9746;
        //&#x2610;
    }

    render() {
        return (
            <div className="container">
                <div className="col-12">
                    <ul className="list-group">
                        <li className="list-group-item d-flex justify-content-between align-items-center">
                            <h4>Diagnostyka systemu</h4>
                            <button onClick={this.checkSystem} type="button" className="btn btn-info">Rozpocznij test</button>
                        </li>
                        <li className="list-group-item d-flex justify-content-between align-items-center">
                            Połączenie z serwerem
                        <span className="badge badge-success badge-pill">{this.state.connectionCharacter}</span>
                        </li>
                        <li className="list-group-item d-flex justify-content-between align-items-center">
                            Połączenie z routerem
                        <span className="badge badge-warning badge-pill">{this.state.routerConnectionCharacter}</span>
                        </li>
                        <li className="list-group-item d-flex justify-content-between align-items-center">
                            Połączenie serwera z kamerą [nazwa kamery 1]
                        <span className="badge badge-danger badge-pill">{this.state.camera1Character}</span>
                        </li>
                    </ul>
                </div>
            </div>

        );
    }
}

export default Diagnostic;