import React, { Component } from "react";

class Porter extends Component {
    constructor(props) {
        super(props);
        this.state = {
            from: '00:00',
            to: '00:00',
            number: '0'
        };
    }

    handleChangeFrom = (event) => {
        this.setState({
            from: event.target.value
        })
    }

    handleChangeTo = (event) => {
        this.setState({
            from: event.target.value
        })
    }

    handleSubmit = () => {
    }

    render() {
        return (
            <div className="container-md">
                <div className="row justify-content-around">
                    <div className="col-12 input-group justify-content-center">
                        <div className="input-group-prepend">
                            <label className="input-group-text" htmlFor="inputGroupSelect02">Od:</label>
                        </div>
                        <input type="time" onChange={this.handleChangeFrom}></input>
                    </div>
                    <div className="col-12 input-group justify-content-center">
                        <div className="input-group-prepend">
                            <label className="input-group-text" htmlFor="inputGroupSelect02">Do:</label>
                        </div>
                        <input type="time" onChange={this.handleChangeTo}></input>
                    </div>
                </div>
                <br></br>
                <div className="row justify-content-around">
                    <button type="button" class="btn btn-primary" onSubmit={this.handleSubmit}>Sprawdź</button>
                </div>
                <hr></hr>
                <div className="row justify-content-around">
                    <div className="alert alert-dismissible alert-info">
                        <strong>Liczba osób wykrytych przez kamerę:</strong><br></br>
                        <h3>{this.state.number}</h3>
                    </div>
                </div>
                <hr></hr>
            </div >
        );
    }
}

export default Porter;