import React, { Component } from 'react';
import Chart from './Chart'
import StatisticsMenu from './StatisticsMenu';
import UnstandardizedDate from './UnstandardizedDate';


export class Statistics extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedChart: 'today',
            earlyDate: null,
            lateDate: null
        };
    }

    onChartSelect = (selectedChart) => {
        this.setState({
            selectedChart
        });
    }

    onDateChange = (fistDate, secondDate) => {
        this.setState({
            earlyDate: fistDate,
            lateDate: secondDate
        });
    }

    render() {
        return (
            <div className="container">
                <div className="row justify-content-around">
                    <div className="col-3">
                        <StatisticsMenu chartType={this.state.chartType} selectChart={this.onChartSelect}/>
                    </div>
                    <div className="col-8">
                        <div className="row">
                            <UnstandardizedDate />
                        </div>

                        <br></br>
                        <Chart labels={this.props.labels} numbers={this.props.numbers} selectedChart={this.props.selectedChart} onDateChange={this.onDateChange} />
                    </div>
                </div>
            </div>
        );
    }
}

export default Statistics;