import React, { Component } from "react";
import axios from 'axios';
import Diagnostic from "./Diagnostic";

class Administrator extends Component {
    constructor(props) {
        super(props);
        this.state = {
            camera1: false,
            camera2: false,
        };
    }

    render() {
        return (
            <div className="col">
                <Diagnostic />
                <br></br>
                <div className="container">
                    <div className="col-12">
                        <div className="card mb-3">
                            <div className="card-header"><h4>Opcje administratora</h4></div>
                            <div className="card-body">
                                <div className="input-group mb-3">
                                    <div className="input-group-prepend">
                                        <button className="btn btn-primary" type="button">Włacz kamerę</button>
                                    </div>
                                    <select className="custom-select" id="inputGroupSelect03" aria-label="Example select with button addon">
                                        <option value="1">Kamera 1</option>
                                    </select>
                                </div>
                                <div className="input-group mb-3">
                                    <div className="input-group-prepend">
                                        <button className="btn btn-warning" type="button">Resetuje kamerę</button>
                                    </div>
                                    <select className="custom-select" id="inputGroupSelect03" aria-label="Example select with button addon">
                                        <option value="1">Kamera 1</option>
                                    </select>
                                </div>
                                <div className="input-group mb-3">
                                    <div className="input-group-prepend">
                                        <button className="btn btn-danger" type="button">Wyłącz kamerę</button>
                                    </div>
                                    <select className="custom-select" id="inputGroupSelect03" aria-label="Example select with button addon">
                                        <option value="1">Kamera 1</option>
                                    </select>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Administrator;